<template>
  <div :style="slidePosition" class="z-6 w-100 h-100">
    <div id="cubes1Yellow" class="position-absolute"
      style="height:100vh; width:100vh;left:100%;transform:translateX(-100%)">
      <canvas class="w-full h-full" id="cubes1YellowCanvas"></canvas>

    </div>
  </div>
</template>

<script>
import anime from "animejs"
//import { normalize } from "@/utils/ranges"

import { slidePositionParallax } from "@/utils/slidePositioning"

import * as THREE from "three"

export default {
  data() {
    return {}
  },
  methods: {
    canvasMove(percentage) {
      let animation = anime
        .timeline({
          autoplay: false,
          loop: false,
          endDelay: 1000
        })
        .add({
          targets: "#cubes1Yellow",
          left: ["100%", "40%"],
          easing: "easeInOutSine",
          duration: 500,
          delay: 1000
        })
      /* var progress =
        percentage <= 0.5 ? percentage * 2 : normalize(percentage, 0.5, 1, 1, 0)
      animation.seek(animation.duration * progress) */
      animation.seek(animation.duration * percentage)
    }
  },
  watch: {
    innerPosition() {
      this.canvasMove(this.innerPosition)
    }
  },
  props: ["globalUnits", "slideIndex"],
  computed: {
    slidePosition() {
      //console.log("SLIDES INDEX", this.slideIndex)
      return slidePositionParallax(this.globalUnits, this.slideIndex)
    },
    innerPosition() {
      return (
        (this.globalUnits - this.slideIndex.start) /
        (this.slideIndex.end - this.slideIndex.start)
      )
    }
  },
  mounted() {
    const canvasContainer = document.getElementById("cubes1Yellow")
    const canvas = document.getElementById("cubes1YellowCanvas")
    var width = canvasContainer.scrollWidth
    var height = canvasContainer.scrollHeight

    var centerX = canvasContainer.scrollWidth / 2
    var centerY = canvasContainer.scrollHeight / 2

    var renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
      gammaFactor: 2.2,
      powerPreference: "high-performance",
      outputEncoding: THREE.sRGBEncoding,
      canvas: canvas
    })
    renderer.setPixelRatio(
      window.devicePixelRatio > 2 ? 2 : window.devicePixelRatio
    )
    //renderer.setPixelRatio(window.devicePixelRatio)

    renderer.setSize(width, height)
    canvasContainer.appendChild(renderer.domElement)

    var scene = new THREE.Scene()

    var cubeGeometry0 = new THREE.BoxBufferGeometry(
      width / 3,
      width / 3,
      width / 3,
      4,
      4,
      4
    )
    const textureCube1 = new THREE.TextureLoader().load(
      "images/Cubes/texture-cube-1-OPT.jpg"
    )
    const textureCube2 = new THREE.TextureLoader().load(
      "images/Cubes/texture-cube-2-OPT.jpg"
    )

    const textureCube3 = new THREE.TextureLoader().load(
      "images/Cubes/texture-cube-3-OPT.jpg"
    )

    var YellowMaterialTexture = new THREE.MeshLambertMaterial({
      color: 0xffcb06
    })

    var cube1MaterialTexture = new THREE.MeshLambertMaterial({
      map: textureCube1
    })
    var cube2MaterialTexture = new THREE.MeshLambertMaterial({
      map: textureCube2
    })
    var cube3MaterialTexture = new THREE.MeshLambertMaterial({
      map: textureCube3
    })
    let cube1MaterialArray
    let cube2MaterialArray
    let cube3MaterialArray
    /* if (window.screen.availWidth > 1280) { */
    cube1MaterialArray = [
      cube1MaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture
    ]
    cube2MaterialArray = [
      YellowMaterialTexture,
      YellowMaterialTexture,
      cube2MaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture
    ]

    cube3MaterialArray = [
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      YellowMaterialTexture,
      cube3MaterialTexture,
      YellowMaterialTexture
    ]
    /* } else {
      cube1MaterialArray = YellowMaterialTexture
      cube2MaterialArray = YellowMaterialTexture

      cube3MaterialArray = YellowMaterialTexture
    } */

    var cube0 = new THREE.Mesh(cubeGeometry0, cube1MaterialArray)
    var cube1 = new THREE.Mesh(cubeGeometry0, cube2MaterialArray)
    var cube2 = new THREE.Mesh(cubeGeometry0, cube3MaterialArray)

    var camera
    cube0.rotation.x = (Math.PI * 45) / 180
    cube0.rotation.y = (Math.PI * 45) / 180
    cube0.rotation.z = (Math.PI * 45) / 180
    cube0.position.x = centerX + Number(width * 0.35)
    cube0.position.y = centerY + Number(height * 0.25)
    cube0.position.z = 500
    scene.add(cube0)

    cube1.rotation.x = (Math.PI * 45) / 180
    cube1.rotation.y = (Math.PI * 45) / 180
    cube1.rotation.z = (Math.PI * 45) / 180
    cube1.position.x = centerX + Number(width * 0.25)
    cube1.position.y = centerY - Number(height * 0.3)
    cube1.position.z = 350
    scene.add(cube1)

    cube2.rotation.x = (Math.PI * 45) / 180
    cube2.rotation.y = (Math.PI * 45) / 180
    cube2.rotation.z = (Math.PI * 45) / 180

    cube2.position.x = centerX - Number(width * 0.4)
    cube2.position.y = centerY - Number(height * 0.65)
    cube2.position.z = 150
    scene.add(cube2)

    camera = new THREE.PerspectiveCamera(85, width / height, 0.1, 2000)

    camera.position.x = centerX
    camera.position.y = centerY
    /* console.log(
      "SCREEN AVAIL HEIGHT",
      window.screen.availWidth > 992,
      window.screen.availWidth
    ) */
    if (window.screen.availWidth > 1280) {
      camera.position.z = 1650
    } else if (window.screen.availWidth > 992) {
      camera.position.z = 1250
    } else {
      camera.position.z = 800
    }

    scene.add(camera)

    var pointLight = new THREE.PointLight(0xdbdbdb)
    pointLight.position.set(width / 3, height * 1.5, 1800)

    scene.add(pointLight)

    const light = new THREE.AmbientLight(0x686868) // soft white light
    scene.add(light)

    var clock0 = new THREE.Clock()
    var clock1 = new THREE.Clock()
    var clock2 = new THREE.Clock()

    scene.background = null
    function render() {
      requestAnimationFrame(render)

      scene.background = null
      renderer.setClearColor(0xffffff, 0)
      cube0.rotation.y += clock0.getDelta() / 2
      cube1.rotation.y += clock1.getDelta() / 2
      cube2.rotation.y += clock2.getDelta() / 2

      cube0.rotation.z -= clock0.getDelta() / 2
      cube1.rotation.z -= clock1.getDelta() / 2
      cube2.rotation.z -= clock2.getDelta() / 2

      renderer.render(scene, camera)
    }

    render()
  }
}
</script>
<style lang="scss" scoped>
canvas {
  width: 100%;
  height: 100%;
}

.z-99 {
  z-index: 999;
}
</style>
